.reservation {
    display: block;

    /* width: 99%; */
    height: fit-content;

    margin: 1px;


    border: 2px solid;
    border-radius: 3px;

    text-align: left;
}

.reservation:hover {
    cursor: pointer;
}

.reservation span {
    margin-left: 1em
}

.reservation-detailed {
    padding-left: 3em;
    text-align: left;
    font-style: italic;
}